import { NextResponse } from 'next/server';

export async function POST(req: Request) {
  try {
    const formData = await req.formData();
    const imageFile = formData.get('image') as File;

    if (!imageFile) return NextResponse.json({ error: "No image" }, { status: 400 });

    const arrayBuffer = await imageFile.arrayBuffer();

    // ðŸ”¥ This is a highly stable model endpoint
    const MODEL_URL = "https://api-inference.huggingface.co/models/briaai/RMBG-1.4";

    const response = await fetch(MODEL_URL, {
      headers: { 
        Authorization: `Bearer ${process.env.HUGGINGFACE_API_TOKEN}`,
        "Content-Type": "application/octet-stream",
        // ðŸ”¥ THIS IS THE KEY: Forces the server to wait for the model to load
        "x-wait-for-model": "true", 
      },
      method: "POST",
      body: arrayBuffer,
    });

    // If it still says 503, it means it's taking longer than 30 seconds to wake up
    if (response.status === 503) {
      return NextResponse.json({ error: "AI is still waking up. Please try one more time!" }, { status: 503 });
    }

    if (!response.ok) {
      const errorText = await response.text();
      console.error("HF Error:", errorText);
      throw new Error("AI is currently busy");
    }

    const blob = await response.blob();
    return new NextResponse(blob, {
      headers: { 'Content-Type': 'image/png' },
    });
  } catch (error: any) {
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}